.stop {
  background: rgba(255, 255, 255, 0.8);
  border-radius: 0;
  border-top: 1px solid black; }

.stop-wrapper {
  margin-bottom: 0; }

table {
  width: 100%; }

table th, table td {
  padding: 10px;
  text-align: left; }

.stops-reload-button {
  position: absolute;
  right: 7px;
  top: -2px; }

.reload-button {
  background: darkgrey;
  display: inline-block;
  padding-top: 3px;
  padding-bottom: 2px;
  padding-left: 5px;
  padding-right: 5px;
  cursor: pointer; }

.reload-button span {
  vertical-align: middle;
  line-height: 25px;
  text-align: center; }

.count-down-label {
  margin-left: 5px;
  font-size: 16px; }

.bookmark-section {
    position: relative;
}

ul.bookmark-section-bookmarks {
    list-style: none;
    padding: 0;
    margin: 0;
    margin-top: 10px;
}

.bookmark-flex-container {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
}


.bookmark-stop-flex-item {
    -webkit-flex-grow: 3;
            flex-grow: 3
}

.bookmark-remove-flex-item {
    padding-left: 10px;
}

.bookmark-section-add-more {
    text-align: center;
    color: white;
    font-size: 14px;
}

.bookmark-stop-wrapper {
    margin-bottom: 10px;
}

.bookmark-section-bookmarks li:last-child .bookmark-stop-wrapper {
    margin-bottom: 0;
}

.bookmark-section-nav-wrapper {
    margin-bottom: 0;
}

.bookmark-section-control-wrapper {
    margin-bottom: 10px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: nowrap;
            flex-wrap: nowrap;
    -webkit-align-content: flex-start;
            align-content: flex-start;
}

.edit-toggle-button {
    background: rgb(0,0,0, 0.5);
    border: none;
    font-size: 16px;
    cursor: pointer;
    height: 30px;
    color: white;
}

.edit-toggle-button:hover {
    background: rgb(0,0,0, 0.70);
}

.bookmark-section-name-wrapper {
    -webkit-flex-grow: 3;
            flex-grow: 3;
    -webkit-order: 0;
            order: 0;
}

.bookmark-section-edit-controls-wrapper {
    -webkit-order: 1;
            order: 1;
    margin-left: 15px;
    display: -webkit-flex;
    display: flex;
}

.edit-section-name-input {
    background: rgb(255,255, 255, 0.70);
    width: 100%;
    color: black;
    border-radius: 5px;
    font-size: 16px;
    line-height: 23px;
}

